.App {
  display: grid;
  grid-template-columns: 1fr 0.875fr 0.05fr 0.875fr 1fr;
  grid-template-rows: 1fr;
  height: 100vh;
  width: 100vw;
}

.inputsContainer {
    grid-column: 4 / 5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.containerChild {
    box-sizing: border-box;
    margin-bottom: 5%;
    font-size: 1.5em;
}

.nickname {
  width: 100%;
  height: 4%;
}

.messageTextArea {
    width: 100%;
    resize: none;
}

.sendButton {
    width: 100%;
    height: 3%;
    border: none;
    background-color: black;
    color: white;
    font-size: 1em;
    transition: all 0.3s;
}

.sendButton:hover {
    background-color: pink;
    color: black;
    border: 1px solid black;
    transition: all 0.3s;
}

.chatHistory {
    border: 1px solid black;
    /* box-sizing: border-box; */
    grid-column: 2 / 3;
    overflow-y: scroll;
}

.chatHeader {
    width: 100%;
    text-align: center;
}

.chatMessage {
    animation-name: appear;
    animation-duration: 0.5s;
}

@keyframes appear {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}